.App {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  color: #222; }

.logo {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 0; }

.logo_dot {
  background-color: transparent;
  position: relative;
  top: 330px;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 5px solid #222;
  margin: 15px;
  -webkit-box-shadow: inset 0 2px 7px 2px rgba(50, 50, 50, 0.2), 0 2px 7px 2px rgba(50, 50, 50, 0.2);
          box-shadow: inset 0 2px 7px 2px rgba(50, 50, 50, 0.2), 0 2px 7px 2px rgba(50, 50, 50, 0.2); }

@media only screen and (max-width: 768px) {
  .logo {
    top: -100px; }
  .logo_dot {
    height: 30px;
    width: 30px; } }

.searchbar {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 57%;
  left: 50%;
  border: none;
  outline-offset: none;
  outline: none;
  outline-color: none;
  -webkit-box-shadow: 0 1px 10px 3px rgba(70, 70, 70, 0.1);
          box-shadow: 0 1px 10px 3px rgba(70, 70, 70, 0.1);
  width: 500px;
  padding: 16px 24px;
  font-size: 1.4em;
  font-weight: 300;
  z-index: 1; }

::-webkit-input-placeholder {
  color: #bbb; }

@media only screen and (max-width: 768px) {
  .searchbar {
    width: 280px;
    margin-left: 10px; } }



